/*Burger-----------------------------------------*/
.burger {
  width: 31px;
  height: 16px;
  display: block;
  position: fixed;
  top: 45px;
  right: 5%;
  z-index: 11;
  cursor: pointer; }
  @media (min-width: 1200px) {
    .burger {
      display: none; } }
  @media (min-width: 580px) {
    .burger {
      top: 62px; } }

.burger:before {
  content: '';
  display: block;
  height: 3px;
  width: 26px;
  background: #7a8899;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(0deg);
  transform: translateX(0);
  -webkit-transition: all 0.3s cubic-bezier(0.215, 0.61, 0.356, 1);
  transition: all 0.3s cubic-bezier(0.215, 0.61, 0.356, 1); }

.burger:after {
  content: '';
  display: block;
  height: 3px;
  width: 31px;
  background: #7a8899;
  position: absolute;
  top: 12px;
  left: 0;
  transform: rotate(0deg);
  -webkit-transition: all 0.3s cubic-bezier(0.215, 0.61, 0.356, 1);
  transition: all 0.3s cubic-bezier(0.215, 0.61, 0.356, 1); }

.burger:hover:before {
  transform: translateX(6px); }

.menu-active .burger:after {
  top: 6px;
  width: 26px;
  transform: rotate(45deg); }

.menu-active .burger:before {
  top: 6px;
  transform: rotate(-45deg); }

/*Menu----------------------------------------*/
.menu-active .menu {
  transform: translateX(0);
  -webkit-transform: translateX(0); }

@media (max-width: 1200px) {
  .menu {
    overflow: hidden;
    background-color: #1c1a25;
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    z-index: 4;
    display: block;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    -webkit-transition: all 0.3s cubic-bezier(0.215, 0.61, 0.356, 1);
    transition: all 0.3s cubic-bezier(0.215, 0.61, 0.356, 1); } }

