  display: inline-block;
  position: relative;
  overflow: hidden;
  height: 45px;
  width: 180px;
  animation-duration: .5s;
  animation-name: worditemappear;
  animation-fill-mode: backwards;
  animation-delay: .2s; }

.padding-word {
