.wordlist {
  display: block;
  position: relative;
  height: 45px; }
  .wordlistitem {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    display: none;
    transition: opacity 800ms cubic-bezier(0.76, -0.01, 0.24, 0.98), transform 800ms cubic-bezier(0.76, -0.01, 0.24, 0.98); }
  .wordlistitem.current {
